import { tail } from "components/Message/Message";
import { Tag } from "components/Tag/Tag";
import * as h from "lib/html";
import * as s from "./NewMessage.scss";
export class NewMessage extends Tag {
    constructor(props) {
        super({
            ...props,
            tag: h.div(h.className(s.root))
        });
        this.emoji = h.i(h.className(`i-smile ${s.emoji}`));
        this.attach = h.i(h.className(`i-attach ${s.attach}`));
        this.back = h.div(h.className(s.back));
        this.placeholder = h.div(h.className(s.placeholder), "Message").hide();
        this.input = h.div(h.className(s.input), h.contenteditable, h.spellcheckOff, h.autocapitalizeOff, h.autocompleteOff, h.autocorrectOff, h.onInput(() => this.onInput()));
        this.bable = h.div(h.className(s.bable), this.emoji, this.placeholder, this.input, this.attach, tail(true, false));
        this.sendBack = h.div(h.className(s.sendBack));
        this.send = h.div(h.className(`i-microphone ${s.send}`), this.sendBack);
        this.content = h.div(h.className(s.content), this.back, this.bable, this.send);
        this.defer = [];
        this.onInput = () => {
            if (this.input.tag.innerText === "")
                this.placeholder.show();
            else
                this.placeholder.hide();
        };
        this.append(this.content);
        this.onInput();
    }
    destroy() {
        this.defer.map(v => v());
        this.defer = [];
    }
}
