import { Tag } from "components/Tag/Tag";
import * as h from "lib/html";
import * as s from "./RightPanel.scss";
export class RightPanel extends Tag {
    constructor(props) {
        super({
            ...props,
            tag: h.div(h.className(s.root))
        });
        this.defer = [];
    }
    destroy() {
        this.defer.map(v => v());
        this.defer = [];
    }
}
