import { Chat } from "components/Chat/Chat";
import { LeftPanel } from "components/LeftPanel/LeftPanel";
import { MediaStore } from "components/Message/MediaStore";
import { NoChat } from "components/NoChat/NoChat";
import { RightPanel } from "components/RightPanel/RightPanel";
import { Tag } from "components/Tag/Tag";
import * as h from "lib/html";
import { ChatListStore } from "./ChatListStore";
import * as s from "./Main.scss";
export class Main extends Tag {
    constructor(props) {
        super({
            ...props,
            tag: h.div(h.className(s.root))
        });
        this.mediaStore = new MediaStore({ apiInvoker: this.props.apiInoker });
        this.defer = [];
        this.chatListStore = new ChatListStore({
            apiInvoker: this.props.apiInoker,
            userStore: this.props.userStore
        });
        this.leftPanel = new LeftPanel({
            apiInoker: this.props.apiInoker,
            chatListStore: this.chatListStore
        });
        this.rightPanel = new RightPanel({
            apiInoker: this.props.apiInoker,
            hide: true
        });
        this.noChat = null;
        this.chat = null;
        this.onDialogChange = () => {
            let dialog = this.chatListStore.activeDialog;
            if (!dialog) {
                if (!this.noChat) {
                    this.removeContent();
                    this.noChat = new NoChat({
                        apiInoker: this.props.apiInoker
                    });
                    this.leftPanel.tag.after(this.noChat.mount());
                }
                return;
            }
            this.removeContent();
            // this.chatListStore.idFromPeer(dialog)
            let chatStore = this.chatListStore.chatStores.get(dialog);
            if (!chatStore)
                return;
            chatStore.init();
            this.chat = new Chat({
                apiInoker: this.props.apiInoker,
                chatListStore: this.chatListStore,
                chatStore,
                mediaStore: this.mediaStore,
                userStore: this.props.userStore
            });
            this.leftPanel.tag.after(this.chat.mount());
        };
        this.defer.push(() => this.chatListStore.destroy());
        this.defer.push(() => this.leftPanel.destroy());
        this.defer.push(() => this.rightPanel.destroy());
        this.defer.push(() => this.removeContent());
        this.append(this.leftPanel, this.rightPanel);
        this.onDialogChange();
        this.defer.push(this.chatListStore.onDialog(this.onDialogChange));
    }
    destroy() {
        this.defer.map(v => v());
        this.defer = [];
    }
    removeContent() {
        if (this.noChat) {
            this.noChat.remove();
            this.noChat.destroy();
            this.noChat = null;
        }
        if (this.chat) {
            this.chat.remove();
            this.chat.destroy();
            this.chat = null;
        }
    }
}
