import IMask from "imask";
import { Tag } from "components/Tag/Tag";
import * as h from "lib/html";
import * as s from "./Input.scss";
export class Input extends Tag {
    constructor(props) {
        super({
            tag: h.div(h.className(s.root), props.forWrap),
            ...props
        });
        this.mask = null;
        this.input = h.input(h.autocapitalizeOff, h.autocompleteOff, h.autocorrectOff, h.spellcheckOff, h.onFocus(() => this.addClass(s.focus)), h.onBlur(() => this.removeClass(s.focus)), this.props.forInput);
        this._onChange = [];
        this.label = h.label(this.props.forLabel);
        this.error = h.label(h.className(s.error));
        this.doMask = (mask) => {
            if (this.mask)
                this.mask.updateOptions({ mask });
            else {
                this.mask = IMask(this.input.tag, {
                    mask
                });
                this.mask.on("accept", () => {
                    this.processValue();
                    if (this.isComplete())
                        for (let cb of this._onChange)
                            cb(this.value);
                });
            }
        };
        this.processValue = () => {
            if (this.value !== null)
                this.addClass(s.withValue);
            else
                this.removeClass(s.withValue);
        };
        if (this.props.theme)
            this.addClass(s[this.props.theme]);
        this.addClass(s.root);
        this.append(this.label, this.error, this.input);
        if (props.iconRight) {
            this.append(props.iconRight);
            props.iconRight.addClass(s.iconRight);
            this.addClass(s.withIconRight);
        }
        if (props.iconLeft) {
            this.append(props.iconLeft);
            props.iconLeft.addClass(s.iconLeft);
            this.addClass(s.withIconLeft);
        }
        this.processValue();
        if (props.value)
            this.value = props.value;
        this.onChange(this.processValue);
        this.input.tag.id = this.input.tag.id || String(Math.random()).slice(2);
        this.label.tag.htmlFor = this.input.tag.id;
        this.error.tag.htmlFor = this.input.tag.id;
    }
    getBoundingClientRect() {
        return this.input.tag.getBoundingClientRect();
    }
    scrollIntoView() {
        this.input.tag.scrollIntoView();
    }
    maskValue(value) {
        value = value === null ? "" : String(value);
        return value;
    }
    setError(text) {
        this.error.tag.innerText = text;
        this.addClass(s.hasError);
    }
    clearError() {
        this.removeClass(s.hasError);
    }
    focus() {
        this.input.tag.focus();
    }
    unmaskValue(value) {
        if (value === "")
            return null;
        if (this.props.number) {
            value = value.replace(/[^-\d]/g, "");
            if (value && Number.isFinite(Number(value)))
                return Number(value);
            return null;
        }
        return value;
    }
    isComplete() {
        // return this.input.tag.value === this.maskValue(this.value);
        return true;
    }
    get value() {
        return this.unmaskValue(this.maskValue(this.unmaskValue(this.mask ? this.mask.value : this.input.tag.value)));
    }
    set value(value) {
        console.log(value, this.maskValue(value), this.unmaskValue(this.maskValue(value)));
        value = this.maskValue(this.unmaskValue(this.maskValue(value)));
        console.log(value);
        let wasValue = this.value;
        this.input.tag.value = value;
        this.processValue();
        if ((!wasValue && this.value !== null) ||
            (this.value === null && wasValue)) {
            // disable label animation
            this.label.tag.remove();
            this.append(this.label.tag);
        }
    }
    onChange(cb) {
        this._onChange.push(cb);
        this.input.listen(h.onInput(() => {
            if (this.isComplete())
                cb(this.value);
        }));
    }
    iconRigth(icon) {
        let oldIcon = this.tag.querySelector(`.${s.iconRight}`);
        if (oldIcon)
            oldIcon.remove();
        if (icon) {
            icon.addClass(s.iconRight);
            this.append(icon);
            this.addClass(s.withIconRight);
        }
    }
    addForceFocus() {
        this.addClass(s.forceFocus);
    }
    removeForceFocus() {
        this.removeClass(s.forceFocus);
    }
}
