import { Tag } from "components/Tag/Tag";
import * as h from "lib/html";
import * as s from "./NoChat.scss";
export class NoChat extends Tag {
    constructor(props) {
        super({
            ...props,
            tag: h.div(h.className(s.root))
        });
        this.topIcon = h.div(h.className(`i-chatsplaceholder ${s.topIcon}`));
        this.header = h.div(h.className(s.header), "Open Chat\nor create a new one");
        this.buttonPrivate = h.div(h.className(s.button), h.i(h.className("i-newprivate")), "Private");
        this.buttonGroup = h.div(h.className(s.button), h.i(h.className("i-newgroup")), "Group");
        this.buttonChannel = h.div(h.className(s.button), h.i(h.className("i-newchannel")), "Channel");
        this.buttons = h.div(h.className(s.buttons), this.buttonPrivate, this.buttonGroup, this.buttonChannel);
        this.defer = [];
        this.append(this.topIcon, this.header, this.buttons);
    }
    destroy() {
        this.defer.map(v => v());
        this.defer = [];
    }
}
