import { Tag } from "components/Tag/Tag";
import * as h from "lib/html";
import * as s from "./Button.scss";
export class Button extends Tag {
    constructor(props) {
        super({
            ...props,
            tag: h.button(...(props.props || []))
        });
        this.addClass(s.root);
        if (props.color)
            this.addClass(s[props.color]);
    }
}
