export function GetKeyByFingerPrints(prints) {
    for (let finger of prints) {
        let key = PublicKeys.get(finger.join());
        if (key)
            return key;
    }
    return GetPublicKeyByDc(2);
}
export function GetPublicKeyByDc(dc) {
    return [...PublicKeys.values()][dc - 1];
}
