import { Main } from "components/Main/Main";
import { SignIn } from "components/SignIn/SignIn";
import { Tag } from "components/Tag/Tag";
import * as h from "lib/html";
import * as s from "./Navigator.scss";
export class Navigator extends Tag {
    constructor(props) {
        super({ ...props, tag: h.div(h.className(s.root)) });
        this.signIn = null;
        this.main = null;
        this.defer = [];
        this.deferPage = () => { };
        this.ifLoggedIn = () => {
            this.goToMain();
        };
        this.isLoggedOut = () => {
            this.goToSignIn();
        };
    }
    mount() {
        this.defer.push(this.props.userStore.onLogin(this.ifLoggedIn));
        this.defer.push(this.props.userStore.onLogout(this.isLoggedOut));
        return super.mount();
    }
    remove() {
        this.defer.map(v => v());
        this.defer = [];
        return super.remove();
    }
    goToSignIn() {
        this.deferPage();
        this.signIn = new SignIn({
            apiInoker: this.props.apiInvoker,
            userStore: () => this.props.userStore
        });
        this.append(this.signIn.mount());
        this.deferPage = () => {
            this.signIn.remove();
            this.signIn.destroy();
            this.signIn = null;
        };
    }
    goToMain() {
        this.deferPage();
        this.main = new Main({
            apiInoker: this.props.apiInvoker,
            userStore: this.props.userStore
        });
        this.append(this.main.mount());
        this.deferPage = () => {
            this.main.remove();
            this.main.destroy();
            this.main = null;
        };
    }
}
