import { config } from "const/config";
import { AllDc } from "const/dc";
import { Connection } from "./Connection";
import { OneOf } from "./SchemaHelpers";
export const Disposer = (d) => d;
export class ApiInvoker {
    constructor(props) {
        this._connections = new Map();
        this.dc = Number(localStorage.dc || config.dc);
        this._onMessage = new Map();
        this.reimportAuth = () => {
            for (let [dc, conn] of this._connections) {
                if (dc === this.props.userStore().userDC)
                    continue;
                conn.reimportAuth();
            }
        };
        this.props = props;
    }
    onMessage(m, foo) {
        let id = m.constructor._id;
        let arr = this._onMessage.get(id);
        if (!arr) {
            arr = new Set();
            this._onMessage.set(id, arr);
        }
        arr.add(foo);
        return () => arr.delete(foo);
    }
    connection(dc = this.dc) {
        let connection = this._connections.get(dc);
        if (!connection) {
            connection = new Connection({
                timeStore: this.props.timeStore,
                apiInvoker: this,
                dc
            });
            this._connections.set(dc, connection);
            connection.onNearestDc(dc => {
                this.dc = dc;
                localStorage.dc = dc;
                this.connection(dc); // create if not exists
            });
            connection.init();
        }
        return connection;
    }
    setAuthorization(a) {
        let u = a.get_user();
        if (u instanceof OneOf)
            u = u.unwrap();
        this.props.userStore().setUser(u);
    }
    init() {
        let otherDC = AllDc.filter(v => v.isTest === config.test && v.id !== this.dc).map(v => v.id);
        this.connection();
        setTimeout(() => {
            otherDC.forEach(id => this.connection(id));
        }, 1000);
        this.props.userStore().onLogin(this.reimportAuth);
    }
    async call(req) {
        let conn = this.connection();
        await conn.ready();
        return new Promise(r => conn.send({
            name: Object.getPrototypeOf(req).constructor.name,
            method: req,
            cb: instance => {
                r(instance);
            }
        }));
    }
}
