let CryptoWorker = import("crypto.worker");
const workers = [];
let nextID = 0;
let resolvers = new Map();
let nextWorkerIndex = 0;
function nextWorker() {
    nextWorkerIndex++;
    if (nextWorkerIndex >= workers.length)
        nextWorkerIndex = 0;
    return workers[nextWorkerIndex];
}
class WorkerClient {
    constructor() {
        while (workers.length < 4) {
            let worker = CryptoWorker.then(C => new C.default());
            worker.then(w => w.addEventListener("message", (msg) => {
                let resolve = resolvers.get(msg.data.id);
                resolvers.delete(msg.data.id);
                if (resolve)
                    resolve(msg.data);
            }));
            workers.push(worker);
        }
    }
    async fetch(worker, name, data, transfer, trans) {
        data.id = nextID++;
        data.name = name;
        if (transfer && !trans) {
            trans = [];
            for (let key in transfer) {
                let v = transfer[key];
                if (v instanceof Uint8Array)
                    v = v.slice().buffer;
                else if (v instanceof Uint32Array)
                    v = v.slice().buffer;
                else if (v instanceof ArrayBuffer) {
                }
                else
                    continue;
                data[key] = v;
                trans.push(v);
            }
        }
        let promise = new Promise(r => resolvers.set(data.id, r));
        if (trans) {
            worker.postMessage(data, trans);
        }
        else {
            worker.postMessage(data);
        }
        return promise;
    }
    async pqPrimeFactorization(pq) {
        let p = [];
        for (let worker of workers) {
            p.push(this.fetch(await worker, "pqPrimeFactorization", { pq }));
        }
        let res = await Promise.race(p);
        return [new Uint8Array(res.p), new Uint8Array(res.q)];
    }
    async modPow(x, y, m) {
        let res = await this.fetch(await nextWorker(), "modPow", {
            x,
            y,
            m
        });
        return new Uint8Array(res.a);
    }
    async sha256Hash(data) {
        let res = await this.fetch(await nextWorker(), "sha256Hash", {
            d: data
        });
        return new Uint8Array(res.d);
    }
    async aesEncrypt(data, aesKey, aesIv) {
        let res = await this.fetch(await nextWorker(), "aesEncrypt", {
            d: data,
            k: aesKey,
            i: aesIv
        });
        return new Uint8Array(res.d);
    }
    async rsaEncrypt(finger, data) {
        let res = await this.fetch(await nextWorker(), "rsaEncrypt", {
            f: finger,
            d: data
        });
        return new Uint8Array(res.d);
    }
    async aesDecrypt(data, aesKey, aesIv) {
        let res = await this.fetch(await nextWorker(), "aesDecrypt", {
            d: data,
            k: aesKey,
            i: aesIv
        });
        return new Uint8Array(res.d);
    }
    async verifyDhParams(dh, ga) {
        let res = await this.fetch(await nextWorker(), "verifyDhParams", {
            dh,
            ga
        });
        return res.r;
    }
    async ungzip(d) {
        let res = await this.fetch(await nextWorker(), "ungzip", {
            d
        });
        return new Uint8Array(res.d);
    }
    async srp(password, g_number, p, salt1, salt2, g_b) {
        let res = await this.fetch(await nextWorker(), "srp", {
            password,
            g_number,
            p,
            salt1,
            salt2,
            g_b
        });
        return [new Uint8Array(res.M1), new Uint8Array(res.A)];
    }
}
let worker = null;
export function GetWorker() {
    if (worker === null)
        worker = new WorkerClient();
    return worker;
}
